import React, { useRef, useState } from "react";
import indiastates from '../data/indiaStates.json';
import { setWindowWidth } from "../utilities/Functions";
import { setInitialMapZoom } from "../utilities/Functions";
import { setDragging } from "../utilities/Functions";
import {
    MapContainer,
    useMapEvents,
    GeoJSON,
    ScaleControl
} from "react-leaflet";
import "leaflet/dist/leaflet.css";
import 'leaflet-fullscreen/dist/Leaflet.fullscreen.js';
import 'leaflet-fullscreen/dist/leaflet.fullscreen.css';
import Monsoon_Rain from "../data/Monsoon_Rain.json"
import Monsoon_Track from "../data/Monsoon_Track.json"
import Rivers from "../data/Rivers.json"
import indiaBasin from "../data/indiaBasin.json"
import Flood_Prone_Area from "../data/Flood_Prone_Area.json"
import ExportMapButton from "../components/ExportMapButton";
import LayerControlBtn from "../components/LayerControlBtn";


const Legend = ({ baseLayers }) => {
    const Monsoon_RainLegend = [
        { color: "red", label: "> 2500" },
        { color: "purple", label: "2000-2500" },
        { color: "blue", label: "1500-2000" },
        { color: "green", label: "1000-1500" },
        { color: "orange", label: "500-1000" },
        { color: "yellow", label: "< 500" },

    ];

    const RiversLegend = [
        { color: "blue", label: "Rivers" },
    ];
    const Flood_Prone_AreaLegend = [
        { color: "purple", label: "Flood Prone Area" },
    ];

    const Monsoon_TrackLegend = [
        { color: "brown", label: "Monsoon Track" },
    ];
    const indiaBasinLegend = [
        { color: "darkblue", label: "India Basin" },
    ];

    return (
        <>
            {baseLayers.length > 0 && (
                <div className="legend_container">
                    <h5>Legends</h5>
                    {baseLayers.includes("Monsoon_Rain") && (
                        <div>
                            <p>Monsoon Annual Rainfall (mm)</p>
                            {Monsoon_RainLegend.map((item) => (
                                <div key={item.color} className="legend_item">
                                    <span
                                        className="legend_item_square"
                                        style={{ backgroundColor: item.color }}
                                    />
                                    <span className="legend-label">{item.label}</span>
                                </div>
                            ))}
                        </div>
                    )}
                    {baseLayers.includes("Monsoon_Track") && (
                        <div>
                            {Monsoon_TrackLegend.map((item) => (
                                <div key={item.color} className="legend_item">
                                    <span
                                        className="legend_item_square"
                                        style={{ backgroundColor: item.color }}
                                    />
                                    <span className="legend-label">{item.label}</span>
                                </div>
                            ))}
                        </div>
                    )}

                    {baseLayers.includes("Rivers") && (
                        <div>
                            {RiversLegend.map((item) => (
                                <div key={item.color} className="legend_item">
                                    <span
                                        className="legend_item_square"
                                        style={{ backgroundColor: item.color }}
                                    />
                                    <span className="legend-label">{item.label}</span>
                                </div>
                            ))}
                        </div>
                    )}
                    {baseLayers.includes("indiaBasin") && (
                        <div>
                            {indiaBasinLegend.map((item) => (
                                <div key={item.color} className="legend_item">
                                    <span
                                        className="legend_item_square"
                                        style={{ backgroundColor: item.color }}
                                    />
                                    <span className="legend-label">{item.label}</span>
                                </div>
                            ))}
                        </div>
                    )}
                    {baseLayers.includes("Flood_Prone_Area") && (
                        <div>
                            {Flood_Prone_AreaLegend.map((item) => (
                                <div key={item.color} className="legend_item">
                                    <span
                                        className="legend_item_square"
                                        style={{ backgroundColor: item.color }}
                                    />
                                    <span className="legend-label">{item.label}</span>
                                </div>
                            ))}
                        </div>
                    )}

                </div>
            )}
        </>
    );
};


const FloodMap = () => {
    const [mousePosition, setMousePosition] = useState({ lat: 0, lng: 0 });
    const mapContainerRef = useRef(null);

    const HandleMouseHover = () => {
        useMapEvents({
            mousemove: (e) => {
                setMousePosition(e.latlng);
            },
        });

        return null;
    };




    const mapPolygonColorToDensity = (density => {
        return density > 2500
            ? 'red'
            : density > 2000
                ? 'purple'
                : density > 1500
                    ? 'blue'
                    : density > 1000
                        ? 'green'
                        : density > 500
                            ? 'orange'
                            : 'yellow';
    })
    const choroplethStyle = (feature => {
        return ({
            fillColor: mapPolygonColorToDensity(feature.properties.Annual),
            weight: 1,
            opacity: 1,
            color: 'black',
            dashArray: '2',
            fillOpacity: 0.5
        });
    });


    const [baseLayers, setBaseLayers] = useState(["Monsoon_Rain", "Flood_Prone_Area", "Rivers",]);

    function onEachFeatureMonsoonRain(feature, layer) {
        layer.on("mouseover", function (e) {
            if (feature.properties && feature.properties.Name) {
                const popupContent = `
          <div>
            STATE: ${feature.properties.Name}<br/>
            Winter: ${feature.properties.Winter}<br/>
            Pre_Monsoo: ${feature.properties.Pre_Monsoo}<br/>
            SW__Monsoo: ${feature.properties.SW__Monsoo}<br/>
            Post_Monso: ${feature.properties.Post_Monso}<br/>
            Annual: ${feature.properties.Annual}<br/>
          </div>
        `;
                layer.bindTooltip(popupContent, { sticky: true });
            }
            layer.openTooltip();
        });

        layer.on("mouseout", function () {
            layer.closeTooltip();
        });
    }
    // function onEachFeatureRivers(feature, layer) {
    //     layer.on("mouseover", function (e) {
    //         if (feature.properties && feature.properties.rivname) {
    //             const popupContent = `
    //       <div>
    //       River Name: ${feature.properties.rivname}<br/>
    //       </div>
    //     `;
    //             layer.bindTooltip(popupContent, { sticky: true });
    //         }
    //         layer.openTooltip();
    //     });

    //     layer.on("mouseout", function () {
    //         layer.closeTooltip();
    //     });
    // }

    function onEachFeatureRivers(feature, layer) {
        layer.on("mouseover", function (e) {
            if (feature.properties && feature.properties.rivname) {
                const popupContent = `
          <div>
          River Name: ${feature.properties.rivname}<br/>
          Origin: ${feature.properties.origin}<br/>
          Major Trib: ${feature.properties.major_trib}<br/>
          ba_name: ${feature.properties.ba_name}<br/>
          Sub Basin: ${feature.properties.sub_basin}<br/>
          Shape Length: ${feature.properties.shape_Leng}<br/>
          </div>
        `;
                layer.bindTooltip(popupContent, { sticky: true });
            }
            layer.openTooltip();
        });

        layer.on("mouseout", function () {
            layer.closeTooltip();
        });

        if (feature.properties && feature.properties.rivname) {
            if (feature.properties.shape_Leng > 200000) {
                const popupContent = `
            <div>
            ${feature.properties.rivname}
            </div>
          `;
                layer.bindTooltip(popupContent, {
                    permanent: true,
                    direction: "center",
                    offset: [0, 0],
                    className: "custom-tooltip", // Assign a custom class name
                });

            }

        }
    }


    // function onEachFeatureRivers(feature, layer) {
    //     if (feature.properties && feature.properties.rivname) {
    //       const popupContent = `
    //         <div>
    //           rivname: ${feature.properties.rivname}<br/>
    //         </div>
    //       `;
    //       layer.bindTooltip(popupContent, { permanent: true });
    //     }
    //   }


    const layersItems = [
        { value: "Monsoon_Rain", label: "Monsoon Rain" },
        { value: "Flood_Prone_Area", label: "Flood Prone Area" },
        { value: "Monsoon_Track", label: "Monsoon Track" },
        { value: "Rivers", label: "Rivers" },
        { value: "indiaBasin", label: "River Basin Boundary" },
    ];

    const [singlebaseLayer, setSingleBaseLayer] = useState("");
    const singleLayerItems = [



    ];





    return (
        <div className="main_page_container">
            <div className="row">
                <div className="col-md-3">
                    <div className="info_container">
                        <h3>Flood</h3>
                        <p>
                            Floods are a common natural disaster in India, primarily caused by heavy rainfall, cyclones, or overflowing rivers. The country's vast river systems, including the Ganges, Brahmaputra, and their tributaries, contribute to the occurrence of frequent and severe floods. When excessive rainfall or a sudden release of water overwhelms the capacity of river channels, floodwaters inundate nearby areas, damaging homes, infrastructure, crops, and causing loss of life. To address the challenges posed by floods, India has implemented measures like flood forecasting, construction of embankments and reservoirs, and disaster response plans to ensure the safety and well-being of affected communities.
                        </p>

                    </div>

                </div>

                <div className="col-md-9">
                    <div className="map_container" ref={mapContainerRef}>
                        <MapContainer
                            center={[22, 84]}
                            fullscreenControl={true}
                            zoom={setInitialMapZoom()}
                            style={setWindowWidth()}
                            attributionControl={false}
                            scrollWheelZoom={false}
                            zoomDelta={0.25}
                            zoomSnap={0}
                            minZoom={3}
                            keyboard={false}
                            dragging={setDragging()}
                            // touchZoom={false}
                            doubleClickZoom={false}
                            maxZoom={9}
                        >
                            <HandleMouseHover />
                            <GeoJSON
                                data={indiastates.features}
                                style={{
                                    fillColor: "#84857e",
                                    fillOpacity: 0.5,
                                    weight: 2,
                                    color: "whitesmoke",
                                    interactive: false,
                                }}
                            />

                            <ScaleControl />
                            <LayerControlBtn setBaseLayers={setBaseLayers} baseLayers={baseLayers} layersItems={layersItems} singlebaseLayer={singlebaseLayer} setSingleBaseLayer={setSingleBaseLayer} singleLayerItems={singleLayerItems} />



                            {baseLayers.includes("Monsoon_Rain") && (
                                <GeoJSON
                                    data={Monsoon_Rain.features}
                                    style={choroplethStyle}
                                    onEachFeature={onEachFeatureMonsoonRain}
                                />
                            )}
                            {baseLayers.includes("Flood_Prone_Area") && (
                                <GeoJSON
                                    data={Flood_Prone_Area.features}
                                    style={{
                                        fillColor: "purple",
                                        weight: 1,
                                        color: "purple",
                                        interactive: false,
                                        fillOpacity: 1
                                    }}
                                />
                            )}

                            {baseLayers.includes("Monsoon_Track") && (
                                <GeoJSON
                                    data={Monsoon_Track.features}
                                    style={{
                                        weight: 2,
                                        color: "brown",
                                        interactive: false,
                                    }}


                                />
                            )}

                            {baseLayers.includes("Rivers") && (
                                <GeoJSON
                                    data={Rivers.features}
                                    style={{
                                        fillColor: "none",
                                        weight: 2,
                                        color: "blue",
                                        // interactive: false,
                                    }}
                                    onEachFeature={onEachFeatureRivers}
                                />
                            )}
                            {baseLayers.includes("indiaBasin") && (
                                <GeoJSON
                                    data={indiaBasin.features}
                                    style={{
                                        fillColor: "none",
                                        weight: 2,
                                        color: "darkblue",
                                        interactive: false,
                                    }}
                                />
                            )}





                            <div
                                style={{
                                    position: "absolute",
                                    bottom: 10,
                                    right: 10,
                                    backgroundColor: "white",
                                    padding: 5,
                                }}
                            >
                                Lat: {mousePosition.lat.toFixed(4)}, Long:{" "}
                                {mousePosition.lng.toFixed(4)}
                            </div>
                            <div className="desktop_legend_container">
                                {baseLayers && <Legend baseLayers={baseLayers} />}
                            </div>

                            <ExportMapButton mapContainerRef={mapContainerRef} mapTitle="Natural Disaster: Flood" />
                        </MapContainer>
                        <div className="phone_legend_container">
                            {baseLayers && <Legend baseLayers={baseLayers} />}
                        </div>
                    </div>


                </div>
            </div>
        </div>
    )
}

export default FloodMap